import React from 'react';
import ReactDOM from 'react-dom';
import App from './v6';
import ParticlesBg from "particles-bg";
import {Grid, Typography} from "@mui/material";
import FlipCountdown from "@rumess/react-flip-countdown";
import ChristmasApp from "./christmas";
import {BrowserRouter, HashRouter, Route, Routes} from "react-router-dom";
import GiftPage from "./giftPage";

const countdown = () => {
    return (
        <Grid container justifyContent='center' alignItems='center' style={{height: '100vh', width: '99vw'}}>
            <Grid item>
                <div>
                    <FlipCountdown
                        theme='light'
                        titlePosition='bottom'
                        hideYear
                        hideMonth
                        size='small'
                        endAtZero
                        endAt={new Date(2022, 0, 2, 11, 35, 0).toUTCString()}
                        style={{fontFamily: "'IBM Plex Sans', sans-serif", color: '#000',}}
                    />
                </div>
                <Typography style={{whiteSpace: 'pre-line', textAlign: 'center'}}>
                    {`\n\n12.12.21 UTC - Tomsk\n
                    01:56 : 15
                    02:08 : 3
                    01:52 : -2
                    01:32 : 7
                    02:06 : 3
                    01:44 : 18
                    01:36 : 2`}
                </Typography>
            </Grid>
        </Grid>
    )
}

ReactDOM.render(
    <React.StrictMode>
        {/*<ParticlesBg type="random" num={80} bg={true}/>*/}
        {/*<App/>*/}
        {/*{(Date.now() - new Date(2021, 0, 2, 10, 0) > 0) ? <ChristmasApp/> : countdown()}*/}
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <Routes>
                <Route exact path='/'
                       element={
                           (Date.now() - new Date(2022, 0, 2, 21, 0) > 0)
                               ? <ChristmasApp/>
                               : countdown()
                       }/>
                <Route exact path='*' element={<GiftPage/>}/>
            </Routes>
        </BrowserRouter>
    </React.StrictMode>,
    document.getElementById('root')
);
